import None from "@/components/qy/none.vue"
export default {
	data() {
		return {
			list_empty: false,
			list:''
		};
	},
	components:{
		None
	},
	onLoad() {
		this._load()
	},
	methods: {
		_load() {
			this.$api.http.get('article/type_article?type=5').then(res=>{
				if (res.data=='') {
					this.list_empty = true
				} else {
					this.list = res.data
				}
			})
		},
		jump(id) {
			uni.navigateTo({
				url: '/pages/article/article?id='+id,
			});
		}
	}
}
